ul {
  list-style: none;
}
.modalCard {
  position: relative;
  background-image: radial-gradient(
    circle at 0% 0%,
    #ffe5f7 0,
    #ffd9f3 12.5%,
    #fdcdf0 25%,
    #f0c0ed 37.5%,
    #e0b3eb 50%,
    #cda7ea 62.5%,
    #b79de9 75%,
    #9f96ea 87.5%,
    #8390ea 100%
  );
  width: 50%;
  height: 500px;
  box-shadow: 5px 4px 20px rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  gap: 3%;
  margin-left: 24%;
  border-radius: 30px;
  top: 190px;
}
.description input {
  border: none;
  border-bottom: 2px solid #000000;
  background-color: transparent;
}

.closeInfoModal {
  position: absolute;
  top: 60px;
  right: 80px;
  cursor: pointer;
}

.closeInfoModal:hover {
  cursor: pointer;
}
.description {
  justify-content: center;
  display: grid;
  margin-top: 90px;
}
.description p {
  color: black;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
}
.button_save {
  color: black;
  background-color: rgb(247, 215, 217);
  width: 15%;
  height: 8%;
  position: absolute;
  bottom: 10px;
  right: 80px;
  border-radius: 15px;
  cursor: pointer;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 600;
}
@media only screen and (max-width: 1000px) {
  .modalCard {
    position: relative;
    background-image: radial-gradient(
      circle at 0% 0%,
      #ffe5f7 0,
      #ffd9f3 12.5%,
      #fdcdf0 25%,
      #f0c0ed 37.5%,
      #e0b3eb 50%,
      #cda7ea 62.5%,
      #b79de9 75%,
      #9f96ea 87.5%,
      #8390ea 100%
    );
    width: 78%;
    height: 467px;
    box-shadow: 5px 4px 20px rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    gap: 3%;
    margin-left: 12%;
    border-radius: 30px;
  }
}
